import { Button, Col } from "antd";
import { useState } from "react";
import { CTitle } from "@components";
import styles from "../../styles/mobile-app.module.css";
import CApi from "@api";
import { MobileOutlined } from "@ant-design/icons";

const MobileApp = () => {
  const [isDownload, setIsDownload] = useState(false);
  const downloadApp = async () => {
    setIsDownload(true);
    await CApi.get("api/mobile-app", {
      responseType: "blob",
    })
      .then((res) => {
        const url = window.URL.createObjectURL(new Blob([res.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "asset.apk");
        document.body.appendChild(link);
        link.click();
        setIsDownload(false);
      })
      .catch((err) => {
        setIsDownload(false);
      });
  };
  return (
    <>
      <CTitle title="Mobile App" />
      <Col
        style={{
          flex: 1,
          textAlign: "center",
        }}
      >
        <div className={styles.container}>
          <MobileOutlined className={styles.child} />
        </div>
        <Button
          onClick={() => downloadApp()}
          disabled={isDownload}
          style={{ backgroundColor: "blue", color: "white", height: 40 }}
        >
          {isDownload ? "Downloading..." : "Download Mobile App"}
        </Button>
      </Col>
    </>
  );
};

export default MobileApp;
